<template>
  <div class="my-lawler">
    <v-container>
      <v-row class="text-center bg-primary-gradient">
        <v-col cols="3">
          <v-img
              :src="require('../../assets/icons/calculator01.svg')"
              contain
              height="90"
          />
        </v-col>

        <v-col cols="8" align-self="center">
          <h1 style="font-size: 1.5rem" class="font-weight-bold profile-title">Alcabala</h1>
        </v-col>
      </v-row>

      <v-row class="breadcrumbs">
        <v-col cols="12"></v-col>
      </v-row>

      <v-row>
        <v-col>
          <p>Es un impuesto que grava las transferencias de propiedad de bienes inmuebles urbanos o rústicos a título
            oneroso o gratuito, cualquiera sea su forma o modalidad, inclusive las ventas con reserva de dominio. Está
            obligado al pago el comprador o adquiriente del inmueble. </p>
          <p>No está al impuesto de Alcabala, el tramo comprendido por las mismas 10 UIT del valor del inmueble.</p>
        </v-col>
      </v-row>

      <v-row>
        <v-col cols="12">
          <div class="call-to-action" style="margin: 1rem;">
            <p>Valor venta del inmueble</p>
            <v-text-field
                @keypress="isNumber($event)"
                v-model.number="price.total"
                prefix="S/"
                hide-details
            ></v-text-field>
          </div>
          <div class="call-to-action" style="margin: 1rem;">
            <p>Valor de autovaluo</p>
            <v-text-field
                @keypress="isNumber($event)"
                v-model.number="price.auto"
                prefix="S/"
                hide-details
            ></v-text-field>
          </div>
          <div class="text-center">
            <v-btn @click="onSubmit" color="primary" width="200">Calcular</v-btn>
          </div>
        </v-col>
        <v-col cols="4" align-self="center" class="text-center">Resultado</v-col>
        <v-col cols="8">
          <v-text-field
              v-model.number="tax"
              prefix="S/"
              hide-details
              readonly
          ></v-text-field>
        </v-col>
        <v-col cols="9"></v-col>
        <v-col cols="3" class="text-center" @click="goToIndex">
          <v-img
              :src="require('../../assets/icons/home-12.svg')"
              contain
              height="48"
          />
          Regresar
        </v-col>
      </v-row>

    </v-container>
  </div>
</template>

<script>
import axios from "axios"

export default {
  name: 'Calculator01',
  data: () => ({
    uit: 0,
    price: {
      total: 0,
      auto: 0,
    },
    tax: 0,
  }),
  computed: {},
  mounted() {
    this.getUITValue()
  },
  methods: {
    getUITValue() {
      let url = 'settings/1'
      axios.get(url).then((response) => {
        this.uit = parseInt(response.data.value)
      })
    },
    isNumber: function (evt) {
      evt = (evt) ? evt : window.event
      let charCode = (evt.which) ? evt.which : evt.keyCode
      if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
        evt.preventDefault()
      } else {
        return true
      }
    },
    onSubmit() {
      let maxValue = this.price.total > this.price.auto ? this.price.total : this.price.auto
      let minValue = this.uit * 10;
      console.log(minValue)
      console.log(maxValue)

      if (maxValue > minValue) {
        this.tax = (maxValue - minValue) * 0.03;
        console.log(this.tax)
      }

      if (this.tax === 0) {
        this.tax = 'NO ESTÁ AFECTO';
      }
    },
    goToIndex(){
      this.$router.push({name: 'CalculatorIndex'})
    }
  }
}
</script>
